import "vant/es/dialog/style/less";
import _Dialog from "vant/es/dialog";
import "vant/es/switch/style/less";
import _Switch from "vant/es/switch";
import "vant/es/field/style/less";
import _Field from "vant/es/field";
import "vant/es/form/style/less";
import _Form from "vant/es/form";
import "vant/es/cell/style/less";
import _Cell from "vant/es/cell";
import "vant/es/cell-group/style/less";
import _CellGroup from "vant/es/cell-group";
import { computed, defineComponent, ref } from 'vue';
import CustomizeWrapper from '@/components/customizeWrapper.vue';
import { useRoute } from 'vue-router';
import { useStore } from 'vuex';
export default defineComponent({
  name: 'SettingPage',
  components: {
    [_CellGroup.name]: _CellGroup,
    [_Cell.name]: _Cell,
    [_Form.name]: _Form,
    [_Field.name]: _Field,
    [_Switch.name]: _Switch,
    [_Dialog.name]: _Dialog,
    CustomizeWrapper
  },

  setup() {
    const checked = ref(true);
    const route = useRoute();
    const store = useStore();
    const {
      params: {
        creditUserId: creditUserId,
        name,
        idno
      }
    } = route;
    checked.value = computed(() => store.state.interModule.userInfo.firstYxf).value === '1' ? true : false;

    const switchChange = v => {
      console.log(v);
      let params = {
        data: {
          creditUserId,
          firstYxf: v ? '1' : '0'
        }
      };
      store.dispatch('setModule/assignYxf', params);
    };

    const cellClick = () => {
      _Dialog.confirm({
        title: '确定解约？',
        message: '解约后，您绑定的亲情账号也不能 使用信用支付'
      }).then(() => {
        let params = {
          data: {
            authToken: localStorage.getItem('authToken'),
            creditUserId,
            name,
            idno
          }
        };
        store.dispatch('setModule/unsign', params).then();
      }).catch(() => {
        console.log('on cancel');
      });
    };

    return {
      checked,
      cellClick,
      switchChange
    };
  }

});